}
.weui-dialog{position:fixed;display:table;z-index:5000;width:80%;max-width:80vw;top:0;right:0;bottom:0;left:0;margin:auto;background-color:#fff;text-align:center;border-radius:.8vw;overflow:hidden;content:"viewport-units-buggyfill; max-width: 80vw; border-radius: 0.8vw"
}
.weui-dialog__hd{padding:1.3em 1.6em .5em
}
.weui-dialog__hd.with-no-content{padding:1.7em 1.6em
}
.weui-dialog__title{font-weight:400;font-size:4.8vw;content:"viewport-units-buggyfill; font-size: 4.8vw"
}
.weui-dialog__bd{padding:0 1.6em .8em;min-height:10.667vw;font-size:4vw;line-height:1.3;word-wrap:break-word;word-break:break-all;color:#999;content:"viewport-units-buggyfill; min-height: 10.667vw; font-size: 4vw"
}
.weui-dialog__bd:first-child{padding:2.7em 5.333vw 1.7em;color:#353535;content:"viewport-units-buggyfill; padding: 2.7em 5.333vw 1.7em"
}
.weui-dialog__ft{position:relative;line-height:12.8vw;font-size:4.8vw;display:-webkit-box;display:-ms-flexbox;display:flex;content:"viewport-units-buggyfill; line-height: 12.8vw; font-size: 4.8vw"
}
.weui-dialog__ft:after{content:" ";position:absolute;left:0;top:0;right:0;height:1px;border-top:1px solid #d5d5d6;color:#d5d5d6;-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleY(.5);transform:scaleY(.5)
}
.weui-dialog__btn{display:block;-webkit-box-flex:1;-ms-flex:1;flex:1;color:#3cc51f;text-decoration:none;-webkit-tap-highlight-color:rgba(0,0,0,0);position:relative
}
.weui-dialog__btn:active{background-color:#eee
}
.weui-dialog__btn:after{content:" ";position:absolute;left:0;top:0;width:1px;bottom:0;border-left:1px solid #d5d5d6;color:#d5d5d6;-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleX(.5);transform:scaleX(.5)
}
.weui-dialog__btn:first-child:after{display:none
}
.weui-dialog__btn_default{color:#353535
}
.weui-dialog__btn_primary{color:#0bb20c
}
.weui-dialog__btn_warn{color:#e64340
}
.weui-skin_android .weui-dialog{text-align:left;-webkit-box-shadow:0 1.6vw 8vw 0 rgba(0,0,0,.1);box-shadow:0 1.6vw 8vw 0 rgba(0,0,0,.1);content:"viewport-units-buggyfill; -webkit-box-shadow: 0 1.6vw 8vw 0 rgba(0, 0, 0, .1); box-shadow: 0 1.6vw 8vw 0 rgba(0, 0, 0, .1)"
