.vux-fade-enter-active,.vux-fade-leave-active{opacity:1;-webkit-transition:opacity .2s linear;transition:opacity .2s linear
}
.vux-fade-enter,.vux-fade-leave-to{opacity:0
}
.vux-dialog-enter-active{-webkit-animation:a .5s;animation:a .5s
}
.vux-dialog-leave-active{-webkit-animation:b .3s;animation:b .3s
}
@-webkit-keyframes a{
0%{-webkit-transform:scale(1.185);transform:scale(1.185);opacity:0
}
to{-webkit-transform:scale(1);transform:scale(1);opacity:1
}
}
@keyframes a{
0%{-webkit-transform:scale(1.185);transform:scale(1.185);opacity:0
}
to{-webkit-transform:scale(1);transform:scale(1);opacity:1
}
}
@-webkit-keyframes b{
0%{-webkit-transform:scale(1);transform:scale(1);opacity:1
}
to{-webkit-transform:scale(.85);transform:scale(.85);opacity:0
}
}
@keyframes b{
0%{-webkit-transform:scale(1);transform:scale(1);opacity:1
}
to{-webkit-transform:scale(.85);transform:scale(.85);opacity:0
}
}
.vux-mask-enter,.vux-mask-leave-active{opacity:0
}
.vux-mask-enter-active,.vux-mask-leave-active{-webkit-transition:opacity .3s;transition:opacity .3s
}
