.vux-slider{overflow:hidden;position:relative
}
.vux-slider .vux-indicator-right,.vux-slider>.vux-indicator{position:absolute;right:4vw;bottom:2.667vw;content:"viewport-units-buggyfill; right: 4vw; bottom: 2.667vw"
}
.vux-slider .vux-indicator-right>a,.vux-slider>.vux-indicator>a{float:left;margin-left:1.6vw;content:"viewport-units-buggyfill; margin-left: 1.6vw"
}
.vux-slider .vux-indicator-right>a>.vux-icon-dot,.vux-slider>.vux-indicator>a>.vux-icon-dot{display:inline-block;vertical-align:middle;width:1.6vw;height:1.6vw;border-radius:.8vw;background-color:#d0cdd1;content:"viewport-units-buggyfill; width: 1.6vw; height: 1.6vw; border-radius: 0.8vw"
}
.vux-slider .vux-indicator-right>a>.vux-icon-dot.active,.vux-slider>.vux-indicator>a>.vux-icon-dot.active{background-color:#04be02
}
.vux-slider>.vux-indicator-center{right:50%;-webkit-transform:translateX(50%);transform:translateX(50%)
}
.vux-slider>.vux-indicator-left{left:4vw;right:auto;content:"viewport-units-buggyfill; left: 4vw"
}
.vux-slider>.vux-swiper{overflow:hidden;position:relative
}
.vux-slider>.vux-swiper>.vux-swiper-item{position:absolute;top:0;left:0;width:100%;height:100%
}
.vux-slider>.vux-swiper>.vux-swiper-item>a{display:block;width:100%;height:100%
}
.vux-slider>.vux-swiper>.vux-swiper-item>a>.vux-img{display:block;width:100%;height:100%;background:50% no-repeat;background-size:cover
}
.vux-slider>.vux-swiper>.vux-swiper-item>a>.vux-swiper-desc{position:absolute;left:0;right:0;bottom:0;height:1.4em;font-size:4.267vw;padding:5.333vw 13.333vw 3.2vw 3.467vw;margin:0;background-image:-webkit-gradient(linear,left top,left bottom,color-stop(0,transparent),to(rgba(0,0,0,.7)));background-image:linear-gradient(180deg,transparent 0,rgba(0,0,0,.7));color:#fff;text-shadow:0 1px 0 rgba(0,0,0,.5);overflow:hidden;text-overflow:ellipsis;white-space:nowrap;word-wrap:normal;content:"viewport-units-buggyfill; font-size: 4.267vw; padding: 5.333vw 13.333vw 3.2vw 3.467vw"
